:root {
    --primaryColor: rgb(213, 175, 104);
    --secondaryColor: #4D4D4D;
    --sideWidth: 280px;
}

body {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    overflow-x: hidden;

}

* {
    font-family: 'Nunito', sans-serif !important;
}

/*===================================
    All Theme colors start
===================================== */

.primaryColor {
    color: var(--primaryColor);
}

.primaryBg {
    background: var(--primaryColor);
}

.gray {
    color: #999;
}

.green {
    color: #37BA00;
}

.red {
    color: #ff0000;
}

.yellow {
    color: #FFD500;
}

.cyan {
    color: #00B4BA;
}

.purple {
    color: #BE31DF;
}

/*===================================
    General Css Starts Here
===================================== */

a {
    text-decoration: none;
    color: #000;
}

a:hover {
    color: var(--primaryColor);
}

.backTitle {
    display: flex;
    align-items: center;
}

.backLink {
    font-size: 20px;
    color: #000;
    margin: 8px 0px;
    border: none;
    background: none;
    transition: 0.3s linear;
}

.backLink:hover {
    color: var(--primaryColor);
}

.backLink i {
    font-size: 20px;
}

.noWrap {
    white-space: nowrap;
}

/* For input type radio btns */

/* Custom Switch Slider */
.selector {
    display: flex;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
}

.selecotr-item {
    margin: 10px 20px;
}

.selector-item_radio {
    appearance: none;
    display: none;
}

.selector-item_radio:checked+.selector-item_label {
    background-color: var(--primaryColor);
    color: #fff !important;
}

.selector-item_label {
    outline: none;
    border: none;
    color: #000 !important;
    padding: 12px 30px;
    font-size: 14px;
    font-weight: 600;
    min-width: 150px;
    transition: 0.3s ease-in-out;
    border-radius: 50px;
    background: #f9f9f9;
    border: 1px solid #e9e9e9;
    font-family: 'Nunito', sans-serif;
    text-align: center;
}

.created_box {
    position: relative;
}

.created_box button {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
}

.custom_switch input {
    display: none;
}

.custom_switch {
    display: inline-block;
    width: 45px;
    height: 25px;
    margin: 4px;
    transform: translateY(50%);
    position: relative;
}

.custom_switch_slider {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    border-radius: 30px;
    box-shadow: 0 0 0 2px #777, 0 0 4px #777;
    cursor: pointer;
    border: 4px solid transparent;
    overflow: hidden;
    transition: 0.3s;
}

.custom_switch_slider:before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background-color: #777;
    border-radius: 30px;
    transform: translateX(-20px);
    transition: 0.3s;
}

input:checked+.custom_switch_slider:before {
    transform: translateX(20px);
    background-color: limeGreen;
}

input:checked+.custom_switch_slider {
    box-shadow: 0 0 0 2px limeGreen, 0 0 8px limeGreen;
}

/* custom tabs start */
.custom_tabs .nav-pills .nav-link.active,
.custom_tabs .nav-pills .show>.nav-link {
    color: #fff !important;
    background-color: #ED8D37 !important;
}

.custom_tabs .nav-pills .nav-link {
    width: 200px !important;
    border-radius: 99px !important;
    padding: 15px 20px;
    color: #FFFFFF;
    font-weight: 600;
}

.custom_tabs .ul_tabs_wrapper {
