.hasSubButton {
    border: none;
    background: #fff0;
    text-align: left;
    position: relative;
}

.hasSubButton::after {
    font-family: 'Font Awesome 5 Free';
    content: '\f054';
    position: absolute;
    top: 50%;
    right: 1rem;
    transform: translateY(-50%);
    color: #fff;
    font-size: 14px;
    font-weight: 600;
    transition: 0.3s linear;
}

.hasSub.open .hasSubButton::after {
    transform: translateY(-50%) rotate(90deg);
}

.hasSub.open .subMenu {
    display: block;
}

